@import url('https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

/* Fonts
font-family: 'Otomanopee One', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif;
*/

:root {
  --turqBlue: #41E2BA;
  --pinkish: #E86A92;
  --yellowish: #F7E733;
  --offWhite: #F7F7F9;
  --offBlack: #2B2D42;
  --grayish: #dddbde;
  --border: #eaeced;
  --shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.19);
  --shadowHover: 0 6px 8px 0 rgba(0, 0, 0, 0.30);
}

/* ~~~~~~~~~~~~~~~~~~Whole app~~~~~~~~~~~~~~~~~~ */
.App {
  background-color: white; /* default */
  color: black; /* default */
  padding: 0;
  margin: 0;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', sans-serif;
}

p, li, a {
  font-family: 'Roboto', sans-serif;
}

h1 {
  margin: 100px 0px 20px 0px;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
}

h2 {
  font-size: 25px;
  font-weight: 600;
}

.turqButton {
  background-color: var(--turqBlue);
  color: black;
  font-family: 'Poppins', sans-serif;
  font-size: 20px;
  font-weight: bold;
  padding: 10px 25px;
  margin: 0px 10px;
  border-radius: 30px;
  text-decoration: none;
  box-shadow: var(--shadow);
  transition: 0.3s;
}

.turqButton:hover {
  background-color: var(--grayish);
  text-decoration: underline;
  box-shadow: var(--shadowHover);
}

.turqButton:focus {
  outline: black solid 3px;
}