main {
    margin: 0px 50px;
    padding: 50px;
    padding-bottom: 140px;
}

h2 {
    margin: 0px 0px 20px 0px;
}

#photos-display {
    display: flex;
    flex-wrap: wrap;
}

#photos-display img {
    height: 200px;
    width: auto;
    margin: 0 1rem 1rem 0;
    flex: 1 0 auto;
}
  
  
  

