#albums-display {
    display: flex;
    flex-wrap: wrap;
}

#albums-display img {
    width: 400px;
    height: 300px;
    object-fit: cover;
    margin: 10px;
}

.seeAll {    
    text-align: center;
    display: block;
    width: 100px;
    padding: 10px;
    margin: 30px auto;
}
