header {
  background-color: white;
  color: black;
  min-height: 5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
  box-shadow: 0 3px 20px grey;
  position: fixed;
  width: 100%;
}

#logo {
  margin: 0px 60px;
  font-family: "Otomanopee One", sans-serif;
  font-size: 30px;
  font-weight: 600;
  color: black;
  text-decoration: none;
}

.header-section {
  display: flex;
  align-items: center;
}

#profilepic {
  margin: 0px 50px;
  padding: 0;
}

#profilepic img {
  margin: 0;
  padding: 0;
}