.login {
  margin-top: 105px; /*header size*/
  padding-bottom: 140px;
}

.card {
  margin: 125px auto 25px auto;
  padding: 50px 0px 25px 0px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 30%;
  height: 70%;
}

.form {
  height: 80%;
  background-color: transparent;
  padding: 10px 0px 10px 0px;
  width: 100%;
}

.form-title {
  display: block;
  margin: 0px auto 20px auto;
  text-align: center;
}

.form-text {
  display: block;
  padding: 12px 0px;
  width: 80%;
  margin: auto;
}

.form-input {
  display: block;
  width: 75%;
  padding: 12px 5px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0.85;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.loginbtn {
  background-color: #04aa6d;
  color: white;
  display: block;
  padding: 14px 20px;
  margin: 24px auto;
  cursor: pointer;
  width: 80%;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 120%;
}

.link {
  color: rgb(87, 87, 241);
}

.error-display {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.error-font {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  color: #cb5b5b;
  margin-top: 12px;
  margin-bottom: 0px;
}
