footer {
    background-color: var(--offBlack);
    color: white;
    padding: 30px;
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    box-shadow: 0 3px 20px grey;
    width: 100%;
    height: 50px;
}

#logo-footer {
    font-family: 'Otomanopee One', sans-serif;
    color: white;
    font-size: 30px;
    font-weight: 600;
    text-decoration: none;
}

footer ul {
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
}

footer a {
    text-decoration: none;
    list-style-type: none;
    font-size: 20px;
    margin: 0px 15px;
    color: white;
    transition: 0.3s;
}

footer a:hover {
    text-decoration: underline;
    color: grey;
}