body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Fonts
font-family: 'Otomanopee One', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif;
*/

:root {
  --turqBlue: #41E2BA;
  --pinkish: #E86A92;
  --yellowish: #F7E733;
  --offWhite: #F7F7F9;
  --offBlack: #2B2D42;
  --grayish: #dddbde;
  --border: #eaeced;
  --shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.19);
  --shadowHover: 0 6px 8px 0 rgba(0, 0, 0, 0.30);
}

/* ~~~~~~~~~~~~~~~~~~Whole app~~~~~~~~~~~~~~~~~~ */
.App {
  background-color: white; /* default */
  color: black; /* default */
  padding: 0;
  margin: 0;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', sans-serif;
}

p, li, a {
  font-family: 'Roboto', sans-serif;
}

h1 {
  margin: 100px 0px 20px 0px;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
}

h2 {
  font-size: 25px;
  font-weight: 600;
}

.turqButton {
  background-color: #41E2BA;
  background-color: var(--turqBlue);
  color: black;
  font-family: 'Poppins', sans-serif;
  font-size: 20px;
  font-weight: bold;
  padding: 10px 25px;
  margin: 0px 10px;
  border-radius: 30px;
  text-decoration: none;
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.19);
  box-shadow: var(--shadow);
  transition: 0.3s;
}

.turqButton:hover {
  background-color: #dddbde;
  background-color: var(--grayish);
  text-decoration: underline;
  box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.30);
  box-shadow: var(--shadowHover);
}

.turqButton:focus {
  outline: black solid 3px;
}
header {
  background-color: white;
  color: black;
  min-height: 5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
  box-shadow: 0 3px 20px grey;
  position: fixed;
  width: 100%;
}

#logo {
  margin: 0px 60px;
  font-family: "Otomanopee One", sans-serif;
  font-size: 30px;
  font-weight: 600;
  color: black;
  text-decoration: none;
}

.header-section {
  display: flex;
  align-items: center;
}

#profilepic {
  margin: 0px 50px;
  padding: 0;
}

#profilepic img {
  margin: 0;
  padding: 0;
}
main {
    margin: 0px 50px;
    padding: 50px;
    padding-bottom: 140px;
}

h2 {
    margin: 0px 0px 20px 0px;
}

#photos-display {
    display: flex;
    flex-wrap: wrap;
}

#photos-display img {
    height: 200px;
    width: auto;
    margin: 0 1rem 1rem 0;
    flex: 1 0 auto;
}
  
  
  

