/* Fonts
font-family: 'Otomanopee One', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif;
*/

#uploadimage {
    margin: 0px 50px;
    padding: 50px;
    padding-bottom: 160px;
    text-align: center;
}

form {
    width: 50%;
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 30px;
    margin: 0 auto;
    text-align: left;
}

input[type=text] {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    width: 100%;
    padding: 12px 20px;
    margin: 10px 0px 20px 0px;
    display: inline-block;
    border: 1px solid #f7f7f9;
    border-radius: 4px;
    box-sizing: border-box;
}
  
input[type=submit] {
    font-family: 'Poppins', sans-serif;
    font-size: 17px;
    background-color: #41E2BA;
    font-weight: bold;
    padding: 10px 15px;
    margin: 20px 0px;
    border: none;
    border-radius: 10px;
    transition: 0.3s;
}

input[type=submit]:hover {
    background-color: #DDDBDE;
    border: 1px solid black;
    text-decoration: underline;
    cursor: pointer;
}
footer {
    background-color: var(--offBlack);
    color: white;
    padding: 30px;
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    box-shadow: 0 3px 20px grey;
    width: 100%;
    height: 50px;
}

#logo-footer {
    font-family: 'Otomanopee One', sans-serif;
    color: white;
    font-size: 30px;
    font-weight: 600;
    text-decoration: none;
}

footer ul {
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
}

footer a {
    text-decoration: none;
    list-style-type: none;
    font-size: 20px;
    margin: 0px 15px;
    color: white;
    transition: 0.3s;
}

footer a:hover {
    text-decoration: underline;
    color: grey;
}
